<template>
  <div class="garage-vehicles">
    <header
      class="text-center d-flex garage-header bg-light p-2 justify-content-between align-center shadow-sm"
    >
      <h5 class="text-center">Vehicles</h5>
      <img
        class="img-fluid"
        src="@/assets/vehicles.png"
        alt="garage-vehicle-image"
      />
    </header>
    <loader :loading="loading" />
    <main>
      <div class="garage-vehicles-lists">
        <div class="service-list-group"></div>
        <p v-if="vehicles" class="text-center my-3">
          Vehicles Found : {{ vehicles.length }}
        </p>
        <ol class="list-group">
          <router-link
            class="list-group-item d-flex justify-content-between align-items-start py-3"
            tag="a"
            :to="'/service/' + vehicle.vehicleNo"
            v-for="vehicle in vehicles"
            :key="vehicle.vehicleNo"
          >
            <div class="ms-2 me-auto">
              <div>
                <span class="fw-bold me-3 number-plate">{{
                  vehicle.vehicleNo
                }}</span>
                <span v-if="vehicle.vehicleModel">
                  {{ vehicle.vehicleModel }}</span
                >
              </div>
            </div>
          </router-link>
        </ol>
      </div>
    </main>
  </div>
</template>

<script>
import Loader from "../../components/Loader.vue";
import http from "../../common/http-common";
export default {
  components: {
    Loader,
  },
  data() {
    return {
      loading: false,
      vehicles: [],
    };
  },
  async created() {
    this.loading = true;
    const result = await http.get("/vehicles");
    this.vehicles = result.data.vehicles;
    this.loading = false;
  },
};
</script>

<style lang="scss" scoped>
.garage-header {
  img {
    width: 2rem;
    height: 2rem;
  }
}
.number-plate {
  font-family: monospace;
}
</style>
